<template>
    <keep-alive>
        <panel :config="config" class="activity">
            <template slot="header">&nbsp;<span v-if="user">{{ title }}</span></template>
            <template slot="table_responsive">
                <template v-if="activities === null"><LoadingComp></LoadingComp></template>
                <template v-else>
                    <table class="table activity">
                        <thead v-if="pageCount > 1">
                            <tr>
                                <td colspan="3" style="text-align:center;">
                                    <pagination @navigate="getPaginateActivities" :current-page="currentPage" :page-count="pageCount" :lang="lang" :size="1"></pagination>
                                </td>
                            </tr>
                        </thead>
                        <tbody v-if="activities.length > 0">
                            <tr v-for="activity in currentActivities" v-if="games[activity.game_id]" :key="activity.client_id" :id="'activity-' + activity.game_id" class="clickable">
                                <td class="game icon" @click="goToGame(activity.game_id)">
                                    <a :href="urlFor('game-index', {'id': activity.game_id})" :target="isIn3rdParty ? '_blank' : '_self'"><img v-if="games[activity.game_id].icon" :src="games[activity.game_id].icon" class="icon game" @error="gameIconError"/></a>
                                </td>
                                <a :href="urlFor('game-index', {'id': activity.game_id})" :target="isIn3rdParty ? '_blank' : '_self'" style="display:block;width: 100% height:100%">
                                    <td @click="goToGame(activity.game_id)">
                                            <ul>
                                                <li class="game name">{{ games[activity.game_id].name }}</li>
                                                <li><template v-if="['xs', 'xxs'].includes($mq)"><time :date="activity.last_used">{{ $moment.unix(activity.last_used).format('L HH:mm') }}</time></template><template v-else>{{ getTrans('last_used') }} <time :date="activity.last_used">{{ $moment.unix(activity.last_used).format('LLLL') }}</time></template></li>
                                                <div class="chevron-action"><font-awesome-icon icon="chevron-right" size="3x" /></div>
                                                <my-ranking :game="activity.game_id" :config="{expandedlist: true, userId: userProfileId, chevron: false, trans: config.trans}"></my-ranking>
                                            </ul>
                                    </td>
                                </a>
                            </tr>
                        </tbody>
                        <tbody v-else>
                            <tr><td>{{ getTrans('noplay') }}</td></tr>
                        </tbody>
                        <tfoot v-if="pageCount > 1">
                            <tr>
                                <td colspan="3" style="text-align:center;">
                                    <pagination @navigate="getPaginateActivities" :current-page="currentPage" :page-count="pageCount" :lang="lang" :size="1"></pagination>
                                </td>
                            </tr>
                        </tfoot>
                    </table>
                </template>
            </template>
        </panel>
    </keep-alive>
</template>

<script>
import axios from 'axios';
import store from '@/store';
import HTTP from '@/httpapi';
import Games from '@api/games';
import User from '@api/user';
import GenericMixin from '@mixins/generic';
import MyRanking from '@/components/MyRanking';
import Pagination from '@/components/Pagination';

// TODO check webpack handles common chunks correctly so that we can safely
// remove the following imports
// import bus from '@/event_bus'
// import LoadingComp from '@/components/LoadingComp'

export default {
    name: 'user_activities',

    mixins: [GenericMixin],

    computed: {
        title () {
            if (this.user) {
                return this.getTrans(
                    this.$options.name + (this.publicDisplay ? '' : '_public') + '_title',
                    {'%login%': this.user.login_name}
                );
            }
        },

        displayWidth: function () {
            return (this.config.width || typeof this.config.width !== 'string' || !this.config.width.include('px') || parseInt(this.config.width) >= 327 ? this.config.width : '327px');
        },

        pageCount () {
            if (!this.activities) {
                return 0;
            }

            return Math.ceil(this.activities.total / this.itemsPerPage);
        },

        currentActivities () {
            if (!this.activities) {
                return [];
            }

            return this.activities.slice((this.currentPage - 1) * this.itemsPerPage, this.currentPage * this.itemsPerPage);
        },

        userProfileId () {
            return this.config.userProfile.id;
        },

        publicDisplay () {
            return (this.userProfileId === 'me');
        }
    },

    components: {
        'pagination': Pagination,
        'my-ranking': MyRanking
    },

    methods: {
        getActivities (page) {
            let t = this;
            axios.all([
                User.activities(this.userProfileId),
                Games.all(),
                Games.rankables()
            ]).then(axios.spread(function (acts, games, rankables) {
                t.rankables = rankables.data.rankingGames;
                t.games = games.data.games;

                let allActivities;
                t.user = acts.data.user;
                if (store.getters.unloggedMode) {
                    allActivities = acts.data.activities;
                } else {
                    allActivities = acts.data.user.userapps;
                }

                // filter games
                allActivities = allActivities.filter(function (act) {
                    if (t.games[act.game_id]) {
                        return true;
                    }
                });
                allActivities.total = allActivities.length;

                t.activities = allActivities;

                t.getPaginateActivities();
            }));
        },

        isRankable (gameId) {
            return (this.rankables && this.rankables.findIndex(function (gid) { return gid === gameId; }) >= 0);
        },

        getPaginateActivities (page) {
            if (page) {
                this.currentPage = page;
            }
        },
    },

    mounted () {
        HTTP.ifInitialized(this.getActivities);
    },

    data () {
        return {
            itemsPerPage: 5,
            currentPage: 1,
            activities: null,
            games: null,
            rankables: null,
            user: null
        };
    }
};

</script>

<style lang='scss' scoped>
@import '~sass/_variables.scss';
@import '~sass/image.scss';

.panel.activity /deep/ {
    padding: 1em;

    .panel-heading {
        h1 {
            color: map-get($asnet-colors, cyan);
        }
    }

    table.activity tr {
        a {
            color: #333;
            text-decoration: none;
        }

        td {
            border: none;
            vertical-align: middle;

            &:nth-of-type(1) {
                width: 16.66666667%;
            }

            &.icon {
                cursor: pointer;
                text-align: center;

                &:hover {
                    background-color: lightgray;
                }
            }
        }

        &:hover {
            .fa-chevron-right {
                color: white;
            }
        }

        ul {
            list-style: none;
            padding: 0;
            text-decoration: none;

            li {
                font-size: 0.9em;

                &.name {
                    font-weight: 500;
                    font-size: 1.1em;
                }
            }
        }

        .myranking {
            top:0;

            .smallsize:nth-of-type(3).onpodium.ranked span:not(.karma) {
                top: -5px;
            }
            .smallsize:nth-of-type(3).hasprogress.ranked span:not(.karma) {
                top: 3px;
            }

            span {
                top:0;
            }

            .fanet {
                top: -8px;
            }

            .progress {
                height: 15px;
                margin-bottom: 0;
                position: relative;
                top: 4px;
            }
        }
    }
}
</style>



